.navbar-div{
   
    background-color: darkkhaki;
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    position: absolute;
    width: 100%;
    z-index: 5;
    height: 5em;
}
.navbar-div-big{
    background-color: darkkhaki;
    display: grid;
    grid-template-columns:repeat(9,0.5fr);
    align-content: center;
    justify-content: space-between;
    position: absolute;
    width: 95%;
    z-index: 5;
    height: 6em;
    border-radius: 5px;
    margin-top:0.8em ;
    /* box-shadow: 2px 2px 15px black; */
}

.navlink-big{
    text-decoration: none;
    color: white;
    display: grid;
    justify-content: center;
    align-content: center;
    /* font-family: 'Atkinson Hyperlegible', sans-serif; */
    font-family: 'Montserrat', sans-serif;
    font-weight:bold;
    font-size: 1rem;
    transition: all .2s ease-in-out ;
}
.navlink-big:hover{
color: #18f059;
}
.navbarlink{
    text-decoration: none;
    color: inherit;
    
}
.hcs-logo{
    display: grid;
    justify-content: center;
    align-content: center;
}
.logostyle{
    height: 4rem;
    width: 6rem;
}
.logostylebig{
    height: 4.5rem;
    width: 7rem;
}

.sample{
    border: 2px solid red;
    height: 100%;
}

.contact-btn{
    padding: 0.8em;
    background-color: #00ba51;
    border-radius: 25px;
    color: white;
    font-size: 0.9rem;
    font-family: 'Atkinson Hyperlegible', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s ease-in-out;
}
.contact-btn:hover{
    background-color: #134321;
    color: white;
    border: 1px solid white;
}
@media only screen and (max-width:1024px){
